.product-large{
    width: 100%;
}
.form-login label{
    font-size: 12px;
    color: #333;
    font-weight: bold;
    letter-spacing: 2px;
}
.form-login .form-control {
    border-bottom: 1px solid #eee;
    padding: 5px 10px;
    max-width: 100%;
}
.settings-menu .menu-title h3 {
    border-bottom: 1px solid #e5e5e5;
    color: #333333;
    font-size: 16px;
    margin: 0 0 10px;
    padding: 5px 0;
    text-transform: uppercase;
    font-weight: bold;
    text-align: center;
    letter-spacing: 3px;
}
.form-login .btn{
    font-size: 14px;
    font-weight: bold;
    letter-spacing: 3px;
    width: 100%;
}
.form-login .form-control:focus {
    box-shadow: none;
}
@media (min-width: 1200px){
    .container {
        max-width: 970px;
    }
    .header-right-menu > ul > li {
        line-height: 96px;
        padding: 0 12px;
        position: relative;
    }
}
@media (min-width: 992px)
{
    .container {
        max-width: 970px;
    }
}