@import url('https://fonts.googleapis.com/css?family=Lato');
@font-face {
	font-family: 'bebas';
	src:url('../../assets/fonts/BebasNeue-Regular.otf');
	font-weight: normal;
	font-style: normal;
}@font-face {
	font-family: 'bebas';
	src:url('../../assets/fonts/BebasNeue-Bold.otf');
	font-weight: bold;
    font-style: normal;
}
h2{
    margin: 0!important;
    padding: 0!important;
    font-size: 4.4em!important;
    font-weight: 700!important;
    text-transform: uppercase!important;
    font-family: bebas,san-serif;
    color: #000;
    line-height: 90%;
}
h3{
    margin: 0!important;
    padding: 0!important;
    font-size: 2.4em !important;
    font-weight: 700 !important;
    text-transform: uppercase!important;
    font-family: bebas,san-serif;
    color: #000;
    line-height: 90%;
}
.list__table {
    margin-bottom: 1.5em!important;
    width: 100%!important;
}
.header__detail-table{
border-left: 3px solid #000;
margin-left: 1em;
text-align: left;
}
.header__detail-table td{
padding: 0 10px!important;
font-family: bebas,san-serif;
font-size: 20px;
line-height: 120%;
color: #444;
}
.order-print{
    font-size: 16px!important;
    padding-top: 20px!important;
    width: 1000px;
    margin: 0 auto;
    color: #000;
    font-family: Lato,san-serif;
}
.list__table thead th {
    border-top: 2px solid #000;
    border-bottom: 2px solid #000;
    color:#000;
    padding: .15em 0;
    font-family: bebas,san-serif;
    font-size: 24px;
    line-height: 32px;
}

.list__table__project{
    width: 100%;
    border: 1px solid #888;
    border-spacing: 0 10px;
    border-collapse: separate;
    margin-top: 12px;
}

.list__table-workorder__desc {
    min-width: 320px;
}

.list__table__total-price {
    border-left: 0;
    text-align: right;
    font-weight: 700;
    padding-right: 1em;
    font-family: bebas,san-serif;
    font-size: 22px;
    line-height: 120%;
    color:#000;
}

.list__table tfoot tr:first-child td{
    border-top: 2px solid #000;
    padding-top: 14px;
}

.list__table__total-quantity__box{
    border: 1px solid #000;
    display: inline-block;
    width: 90%;
    min-height: 64px;
    padding: 0 1em;
}

.order-print{
    font-size: 16px!important;
    padding-top: 20px!important;
    width: 1000px;
    margin: 0 auto;
    color: #000;
    font-family: Lato,san-serif;
}
.table__project {
    width: 100%;
    border-spacing: 0 10px;
    border-collapse: separate;
}